#page {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.32);
  transition: opacity 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
}

#container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  flex-wrap: wrap;
}

/* common */
.loading {
  font-size: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  text-align: center;
}
.loading span {
  display: inline-block;
  margin: 0 -0.05em;
}

/* code #6 */
.loading06 span {
  position: relative;
  color: black;
  padding: 0 2px;
}
.loading06 span::after {
  position: absolute;
  top: 0;
  left: 0;
  content: attr(data-text);
  color: #fff;
  opacity: 0;
  transform: rotateY(-90deg);
  animation: loading06 4s infinite;
}
.loading06 span:nth-child(2)::after {
  animation-delay: 0.2s;
}
.loading06 span:nth-child(3)::after {
  animation-delay: 0.4s;
}
.loading06 span:nth-child(4)::after {
  animation-delay: 0.6s;
}
.loading06 span:nth-child(5)::after {
  animation-delay: 0.8s;
}
.loading06 span:nth-child(6)::after {
  animation-delay: 1s;
}
.loading06 span:nth-child(7)::after {
  animation-delay: 1.2s;
}
.loading06 span:nth-child(8)::after {
  animation-delay: 1.4s;
}
.loading06 span:nth-child(9)::after {
  animation-delay: 1.6s;
}
.loading06 span:nth-child(10)::after {
  animation-delay: 1.8s;
}

@keyframes loading06 {
  0%, 75%, 100% {
    transform: rotateY(-90deg);
    opacity: 0;
  }
  25%, 50% {
    transform: rotateY(0);
    opacity: 1;
  }
}
